<template>
  <div class="final-success">
    <h2 class="tit-h2">Поздравляем, вы успешно зарегистрировались</h2>
    <br />

    <div class="label-title">Данные:</div>
    <p class="row-table">
      <span>Событие:</span>
      <span>{{ dealData.dealName }}</span>
    </p>
    <p class="row-table">
      <span>Тип регистрации:</span>
      <span>{{
        stepsFields.isAlone ? "В сборный экипаж": "Своя команда"
      }}</span>
    </p>
    <p class="row-table">
      <span>Тип яхты:</span>
      <span>{{ stepsFields.yachtType ? stepsFields.yachtType.btnText : 'Нет' }}</span>
    </p>
    <div class="row-table">
      <span>Интструктор:</span>
      <span>{{dealData.isNeedInstructor}}</span>
    </div>

    <div class="row-table">
      <span>Этапы:</span>
      <span v-html="stagesHtml"></span>
    </div>
    <p class="row-table">
      <span>Номер телефона:</span>
      <span>+{{dealData.phoneNumber}}</span>
    </p>
    <p class="row-table" v-if="dealData.nameOfTeam !== 'нет'">
      <span>Название команды:</span>
      <span>{{dealData.nameOfTeam}}</span>
    </p>
    <p class="row-table">
      <span>Имя:</span>
      <span>{{dealData.name}}</span>
    </p>
    <p class="row-table">
      <span>Фамилия:</span>
      <span>{{dealData.lastName}}</span>
    </p>
    <p class="row-table">
      <span>Дата рождения:</span>
      <span>{{dealData.dateOfBorn}}</span>
    </p>
  </div>
</template>

<script lang="ts" setup>
import { computed } from "vue";
import { useStore } from "vuex";

const store = useStore();

const stepsFields = computed(() => store.state.steps.stepsFields);
const memberForm = computed(() => store.state.steps.memberForm);
const dealData = computed(() => store.state.steps.dealData);

const stagesHtml = computed(() => {
  let text = dealData.value.stages;
  return text.replace(/,/g, "<br>");
});
</script>

<style lang="scss" scoped></style>
