<template>
  <div class="event-selection" v-if="selectedEvent">
    <selectCommandType class="event-selection__step"></selectCommandType>

    <div class="event-selection__container">
      <transition name="fade">
        <selectYachtType
          v-if="isYachtTypeVisible"
          class="event-selection__step"
        ></selectYachtType>
      </transition>
      <transition name="fade">
        <selectStages
          v-if="isStagesVisible"
          class="event-selection__step"
        ></selectStages>
      </transition>
      <transition name="fade">
        <selectInstructor
          v-if="isInstructorVisible"
          class="event-selection__step"
        ></selectInstructor>
      </transition>
      <transition name="fade">
        <memberForm
          v-if="isMemberFormVisible"
          class="event-selection__step"
        ></memberForm>
      </transition>
    </div>
  </div>
</template>

<script lang="ts" setup>
import { computed, ref } from "vue";
import { useStore } from "vuex";

import selectCommandType from "@/components/formSteps/selectCommandType.vue";
import selectYachtType from "@/components/formSteps/selectYachtType.vue";
import selectStages from "@/components/formSteps/selectStages.vue";
import selectInstructor from "@/components/formSteps/selectInstructor.vue";
import memberForm from "@/components/formSteps/memberDataForm.vue";
import { useRoute } from "vue-router";

const store = useStore();

const groups = computed(() => store.state.groups);

const parsedEvents = computed(() => store.state.parsedEvents);

const route = useRoute();
const eventId = computed(() => +route.query.eventId);

const selectedEvent = computed(() => {
  if (parsedEvents.value) {
    const selectedEvent = parsedEvents.value.find(
      (event) => event.id === eventId.value
    );

    store.commit("setSelectedEvent", selectedEvent);

    return selectedEvent;
  }
  return null;
});

const currentStep = ref(0);

const stepsFields = computed(() => store.state.steps.stepsFields);

const isYachtTypeVisible = computed(() => {
  return stepsFields.value.isAlone === false;
});

const isStagesVisible = computed(() => {
  if (stepsFields.value.isAlone === true) {
    return true;
  } else {
    return !!stepsFields.value.yachtType?.id;
  }
});

const isInstructorVisible = computed(() => {
  return stepsFields.value.selectedStages?.length > 0;
});

const isMemberFormVisible = computed(() => {
  return typeof stepsFields.value.instructor === "boolean";
});
</script>

<style lang="scss" scoped>
.event-selection {
  &__container {
    max-width: 746px;
  }

  &__step {
    margin-bottom: 44px;
  }

  @media (max-width: 500px) {
    &__step {
      margin-bottom: 30px;
    }
  }
}

.fade-enter-active, .fade-leave-active {
transition: opacity 0.1s ease-in-out;
}

.fade-enter-from, .fade-leave-to {
opacity: 0;
}
</style>
