import axios from "axios";
import getDayFromDate from "@/functions/getDayFromDate";

function getStagesStr(stages: any) {
  let resultStr = "";

  stages.forEach((stage: any) => {
    resultStr += `${stage.name}(${getDayFromDate(stage.services[0].date)}), `;

    if (stage.name2) {
      resultStr += `${stage.name2}(${getDayFromDate(
        stage.services2[0].date
      )}), `;
    }
  });

  return resultStr;
}

function getAllPrice(stages: any) {
  let resultPrice = 0;
  stages.forEach((stage: any) => {
    resultPrice += stage.services[0].service.price_max;

    if (stage.name2) {
      resultPrice += stage.services2[0].service.price_max;
    }
  });

  return resultPrice;
}
function getPriceList(stages: any) {
  let resultPriceStr = "";
  stages.forEach((stage: any) => {
    resultPriceStr += `${
      stage.name
    }(${stage.services[0].service.price_max.toLocaleString()}₽), `;

    if (stage.name2) {
      resultPriceStr += `${
        stage.name2
      }(${stage.services2[0].service.price_max.toLocaleString()}₽), `;
    }
  });

  return resultPriceStr;
}

function formatDate(inputDate: string) {
  // Разделяем строку даты на компоненты
  const [year, month, day] = inputDate.split("-");

  // Возвращаем дату в новом формате
  return `${day}.${month}.${year}`;
}

export default {
  state: {
    allOrdersCreated: false,
    createdOrdersLoading: false,
    stepsFields: {
      isAlone: null,
      yachtType: null,
      selectedStages: [],
      instructor: null,
      comment: "",
    },
    memberForm: {
      email: "",
      phone: "",
      teamName: "",
      name: "",
      lastName: "",
      dateBorn: "",
    },

    orderData: {
      comment: "",
      email: "",
      fullname: "",
      surname: "",
      phone: "",
      clients_count: 1,
      is_charge_required_priority: true,
      is_newsletter_allowed: true,
      is_personal_data_processing_allowed: true,
      is_support_charge: true,
    },
    dealData: {
      dealName: "",
      typeOfReg: "",
      typeOfYacht: "",
      stages: "",
      isNeedInstructor: "",
      price: "",
      priceList: "",
      email: "",
      phoneNumber: "",
      nameOfTeam: "",
      name: "",
      lastName: "",
      dateOfBorn: "",
    },
  },
  getters: {},
  mutations: {
    fillOrderData(state: any) {
      const memberForm = state.memberForm;
      const stepsFields = state.stepsFields;

      const comment = stepsFields.instructor
        ? `Инструктор нужен, комментарий: ${stepsFields.comment}`
        : "Инструктор не нужен";

      state.orderData = {
        comment: comment,
        email: memberForm.email,
        fullname: memberForm.name,
        surname: memberForm.lastName,
        phone: memberForm.phone,
        is_personal_data_processing_allowed: true,
        clients_count: 1,
        is_charge_required_priority: true,
        is_newsletter_allowed: true,
        is_support_charge: true,
      };
    },
    fillDealData(state: any, rootState: any) {
      const memberForm = state.memberForm;
      const stepsFields = state.stepsFields;

      const comment = stepsFields.instructor
        ? `Инструктор нужен, комментарий: ${stepsFields.comment}`
        : "Инструктор не нужен";

      state.dealData = {
        dealName: rootState.selectedEvent.name,
        typeOfReg: stepsFields.isAlone ? "Я один" : "У меня команда",
        typeOfYacht: stepsFields.yachtType?.position || "нет",
        stages: getStagesStr(stepsFields.selectedStages),
        isNeedInstructor: comment,
        price: getAllPrice(stepsFields.selectedStages),
        priceList: getPriceList(stepsFields.selectedStages),
        email: memberForm.email,
        phoneNumber: memberForm.phone,
        nameOfTeam: memberForm.teamName ? memberForm.teamName : "Нет",
        name: memberForm.name,
        lastName: memberForm.lastName,
        dateOfBorn: formatDate(memberForm.dateBorn),
      };
    },
  },
  actions: {
    createOrders(context: any) {
      context.state.createdOrdersLoading = true;

      const selectedStages = context.state.stepsFields.selectedStages;
      context.state.stepsFields.selectedStages.sort(
        (a: any, b: any) => a.number - b.number
      );

      context.commit("fillOrderData");
      context.commit("fillDealData", context.rootState);

      context.dispatch("getAllGroups", null, { root: true }).then(() => {
        const suitableServicesIds: any[] = [];

        selectedStages.forEach((stage: any) => {
          const suitableService = stage.services.find(
            (service: any) => service.records_count < service.capacity
          );
          const suitableService2 = stage.services2.find(
            (service: any) => service.records_count < service.capacity
          );

          if (suitableService) {
            suitableServicesIds.push(suitableService.id);
          }
          if (suitableService2) {
            suitableServicesIds.push(suitableService2.id);
          }
        });

        console.log(suitableServicesIds);

        const createOrderPromises = suitableServicesIds.map((id, index) => {
          return new Promise((resolve: any) => {
            setTimeout(() => {
              context.dispatch("createOrder", id);
              resolve();
            }, index * 200);
          });
        });

        Promise.all(createOrderPromises).finally(() => {
          // Код, который нужно выполнить после создания всех заказов
          console.log("Все заказы созданы");
          context.state.allOrdersCreated = true
          context.state.createdOrdersLoading = false;
        });
      });
    },
    async createOrder(context: any, activity_id: number) {
      return await axios({
        url: `https://api.yclients.com/api/v1/activity/${context.rootState.salonId}/${activity_id}/book`,
        method: "POST",
        data: context.state.orderData,
        headers: {
          Authorization: context.rootState.auth,
        },
      });
    },
  },
};
