import { Event } from "@/types";

interface Stage {
  number: number;
  name: string;
  name2: string | null;
  services: Event[];
  services2: Event[];
}

type ParsedCategory = {
  id: number;
  name: string;
  newTeam: { [position: string]: Stage[] };
  existingTeam: Stage[];
};

// Основная функция, которая парсит массив событий и возвращает структурированные данные.
function parseGroupsArray(groups: Event[]) {
  const categoryMap: { [key: number]: ParsedCategory } = {};

  groups.forEach((event: Event) => {
    const categoryInfo = parseCategoryTitle(event.service.category.title);
    if (categoryInfo) {
      const { categoryId, name, type } = categoryInfo;

      if (!categoryMap[categoryId]) {
        categoryMap[categoryId] = {
          id: categoryId,
          name,
          newTeam: {},
          existingTeam: [],
        };
      }

      const stageInfo = parseStage(event.service.title);
      if (stageInfo) {
        const { stageNumber, stageName } = stageInfo;
        const positionTitle = event.staff.position.title;

        if (type.toLowerCase().includes("у меня команда")) {
          if (!categoryMap[categoryId].newTeam[positionTitle]) {
            categoryMap[categoryId].newTeam[positionTitle] = [];
          }
          addEventToStage(
            categoryMap[categoryId].newTeam[positionTitle],
            stageNumber,
            stageName,
            event
          );
        }

        if (type.toLowerCase().includes("я один")) {
          addEventToStage(
            categoryMap[categoryId].existingTeam,
            stageNumber,
            stageName,
            event
          );
        }
      }
    }
  });

  // Убедитесь, что все 4 этапа присутствуют
  Object.values(categoryMap).forEach((category) => {
    ["newTeam", "existingTeam"].forEach((teamType) => {
      const team: any = category[teamType as keyof ParsedCategory];
      if (teamType === "newTeam") {
        Object.keys(team).forEach((position) => {
          const stages = team[position];
          if (Array.isArray(stages)) {
            ensureAllStagesPresent(stages);
          }
        });
      } else {
        ensureAllStagesPresent(team as Stage[]);
      }
    });
  });

  const parsedEvents: any = Object.values(categoryMap);
  const staffsPositions: any = extractPositions(groups);

  return {
    parsedEvents,
    staffsPositions,
  };
}

// Функция для извлечения ID, названия и типа из заголовка категории.
function parseCategoryTitle(categoryTitle: string) {
  const match = categoryTitle.match(/^\[(\d+)\] (.+) \/ (.+)$/);
  if (match) {
    const [, id, name, type] = match;
    return { categoryId: parseInt(id, 10), name, type };
  }
  return null;
}

// Функция для извлечения номера и названия этапа из заголовка услуги.
function parseStage(serviceTitle: string) {
  const stageMatch = serviceTitle.match(/^(\d+)/);
  if (stageMatch) {
    return {
      stageNumber: parseInt(stageMatch[1], 10),
      stageName: serviceTitle,
    };
  }
  return null;
}

// Функция для добавления события в соответствующий этап.
function addEventToStage(
  stages: Stage[],
  stageNumber: number,
  stageName: string,
  event: Event
) {
  let stage = stages.find((s: Stage) => s.number === stageNumber);
  if (!stage) {
    stage = {
      number: stageNumber,
      name: stageName,
      name2: null,
      services: [],
      services2: [],
    };
    stages.push(stage);
  }

  const dayMatch = stageName.match(/ДЕНЬ (\d+)$/);
  if (dayMatch && dayMatch[1] === "2") {
    stage.name2 = stageName;
    stage.services2.push(event);
  } else {
    stage.services.push(event);
  }
}

// Функция для обеспечения наличия всех 4 этапов.
function ensureAllStagesPresent(stages: Stage[]) {
  for (let i = 1; i <= 4; i++) {
    if (!stages.some((stage) => stage.number === i)) {
      stages.push({
        number: i,
        name: `${i} ЭТАП`,
        name2: null,
        services: [],
        services2: [],
      });
    }
  }
}

// Функция для извлечения уникальных позиций сотрудников и формирования массива объектов.
function extractPositions(groups: Event[]) {
  const positions = groups.map((event: Event) => event.staff.position.title);
  return Array.from(new Set(positions)).map((position) => {
    const btnTextMatch = position.match(/\(([^)]+)\)/);
    const btnText = btnTextMatch ? btnTextMatch[1] : "";
    const id = Math.floor(Math.random() * 1000000); // Генерация случайного числа
    return { position, btnText, id };
  });
}

export default parseGroupsArray;
