<template>
  <div class="select-command">
    <button
      class="big-btn"
      :class="{ _active: isAlone === false }"
      @click="isAlone = false"
    >
      У меня команда
    </button>
    <button
      class="big-btn"
      :class="{ _active: isAlone === true }"
      @click="isAlone = true"
    >
      Я один
    </button>
  </div>
</template>

<script lang="ts" setup>
import { computed } from "vue";
import { useStore } from "vuex";

const store = useStore();

const isAlone = computed({
  get:() => store.state.steps.stepsFields.isAlone,
  set: (newValue) => {
    store.state.steps.stepsFields.isAlone = newValue;

    if (store.state.steps.stepsFields.isAlone) {
      store.state.steps.stepsFields.isAlone = false
      setTimeout(() => {
        store.state.steps.stepsFields.isAlone = true
      }, 100);
    }

    store.state.steps.stepsFields.selectedStages = []
    store.state.steps.stepsFields.yachtType = null
  },
});
</script>

<style lang="scss" scoped>
.select-command {
  display: flex;
  justify-content: space-between;
  max-width: 873px;

  @media (max-width: 1444px) {
    flex-direction: column;
    gap: 22px;
  }
}
</style>
