import axios from "axios";
import { createStore } from "vuex";
import parseGroupsArray from "@/functions/parseGroupsArray"

import stepsModule from "./modules/steps";

export default createStore({
  state: {
    version: "1.0",
    auth: "Bearer gtcwf654agufy25gsadh",
    salonId: 1268324,
    mainLoader: true,
    groups: [],
    parsedEvents: null,
    staffsPositions: [],
    selectedEvent: null,
  },
  getters: {},
  mutations: {

    parseGroupsArray(state, groups) {
      state.groups = groups;

      const {parsedEvents, staffsPositions} = parseGroupsArray(groups)
      
      state.parsedEvents = parsedEvents;
      state.staffsPositions = staffsPositions;

      state.mainLoader = false
    },
    setSelectedEvent(state, event) {
      state.selectedEvent = event
    }
  },
  actions: {
    async getAllGroups(context) {
      return await axios({
        method: "GET",
        url: `https://api.yclients.com/api/v1/activity/${context.state.salonId}/search?count=1000&from=2025-02-31&till=9999-01-01`,
        headers: {
          Authorization: context.state.auth,
        },
      }).then((res) => {
        context.commit("parseGroupsArray", res.data.data);
      });
    },
  },
  modules: {
    steps: stepsModule
  },
});
