<template>
  <div class="select-yacht" v-if="yachtTypes.length > 0">
    <div class="select-yacht__row">
      <radio-input
        :disabled="!isAvalibleType(yachtTypes[0])"
        name="yachtType1"
        :id="`yachtType123`"
        :value="false"
        @updateValue="isMyYacht = false"
        >{{ yachtTypes[0].btnText }}</radio-input
      >
      <radio-input
        name="yachtType1"
        :id="`yachtTypee347`"
        :value="true"
        v-model="isMyYacht"
        :disabled="
          !isAvalibleType(yachtTypes[1]) && !isAvalibleType(yachtTypes[2])
        "
        >Своя яхта</radio-input
      >
    </div>

    <div class="select-yacht__row" v-if="isMyYacht">
      <radio-input
        :disabled="!isAvalibleType(yachtTypes[1])"
        name="yachtType2"
        :id="`yachtType3465`"
        :value="yachtTypes[1]"
        v-model="selectYathType"
        >{{ yachtTypes[1].btnText }}</radio-input
      >
      <radio-input
        :disabled="!isAvalibleType(yachtTypes[2])"
        name="yachtType2"
        :id="`yachtType986`"
        :value="yachtTypes[2]"
        v-model="selectYathType"
        >{{ yachtTypes[2].btnText }}</radio-input
      >
    </div>
  </div>
</template>

<script lang="ts" setup>
import { useStore } from "vuex";
import radioInput from "@/components/UI/radio-input.vue";
import { computed, ref, watch } from "vue";

const store = useStore();

const yachtTypes = computed(() => store.state.staffsPositions);

const isMyYacht = ref(null);

const stepsFields = computed(() => store.state.steps.stepsFields);

const selectedEvent = computed(() => store.state.selectedEvent);

const avalibleYachtTypes = computed(() =>
  Object.keys(selectedEvent.value.newTeam)
);

function isAvalibleType(type) {
  return avalibleYachtTypes.value.includes(type.position);
}

const selectYathType = computed({
  get: () => stepsFields.value.yachtType,
  set: (newValue) => {
    stepsFields.value.yachtType = newValue;

    stepsFields.value.yachtType = null
    setTimeout(() => {
      stepsFields.value.yachtType = newValue;
    }, 100);
    store.state.steps.stepsFields.selectedStages = [];
  },
});

watch(isMyYacht, (newValue) => {
  if (!newValue) {
    selectYathType.value = yachtTypes.value[0];
  } else {
    selectYathType.value = "";
  }
});
</script>

<style lang="scss" scoped>
.select-yacht {
  display: flex;
  flex-direction: column;
  gap: 49px;
  font-size: 13px;

  &__row {
    display: flex;
    align-items: flex-start;
    gap: 9%;
  }

  .custom-radio {
    width: 40%;
  }

  @media (max-width: 500px) {
    &__row {
      display: flex;
      align-items: flex-start;
      flex-grow: 1;
      gap: 0%;
    }

    .custom-radio {
      width: 50%;
    }
  }
}
</style>
