<template>
  <div class="select-command">
    <h2 class="tit-h2">Отметьте этапы участия (можно несколько):</h2>

    <check-input
      v-for="(stage, index) in showStages"
      :value="false"
      v-model="kostil[index]"
      :id="`selectStage${stage.number}`"
      :key="stage.number"
      @updateValue="(value) => setStage(value, stage)"
      :disabled="isStagePassed(stage)"
      :class="{'_invalid-text': !isHaveFreePlaces(stage)}"
    >
      <p>{{ getStageTitle(stage, 1) }}</p>
      <p v-if="stage.name2">{{ getStageTitle(stage, 2) }}</p>
      <p v-if="!isHaveFreePlaces(stage)" class="_invalid-text">Мест нет, но мы можем добавить вас в <span class="fw-700">лист ожидания</span></p>
    </check-input>
  </div>
</template>

<script lang="ts" setup>
import { useStore } from "vuex";
import checkInput from "@/components/UI/check-input.vue";
import { computed, onMounted, onUnmounted, reactive } from "vue";

import getDayFromDate from "@/functions/getDayFromDate";

const store = useStore();

const kostil = reactive([false, false, false, false, false, false, false]);

const selectedEvent = computed(() => store.state.selectedEvent);
const stepsFields = computed(() => store.state.steps.stepsFields);
const isAlone = computed(() => stepsFields.value.isAlone);
const yachtType = computed(() => stepsFields.value.yachtType);

function getStageTitle(stage, number) {
  if (isStagePassed(stage)) {
    return stage.name;
  } else {
    if (number === 1) {
      const stageName = stage.name;
      const stageDate = getDayFromDate(stage.services[0].date);

      return `${stageName} — ${stageDate}`;
    } else if (number === 2) {
      const stageName = stage.name2;
      const stageDate = getDayFromDate(stage.services2[0].date);

      return `${stageName} — ${stageDate}`;
    }
  }
}

const showStages = computed(() => {
  let result;
  if (isAlone.value) {
    result = selectedEvent.value.existingTeam;
  } else {
    result = selectedEvent.value.newTeam[yachtType.value.position];
  }

  return result.sort((a: any, b: any) => a.number - b.number);
});

function setStage(checkboxValue, stage) {
  if (isStagePassed(stage)) {
    return;
  }
  console.log(checkboxValue);
  const selectedStages = store.state.steps.stepsFields.selectedStages;

  if (checkboxValue) {
    selectedStages.push(stage);
  } else {
    const stageNumber = stage.number;

    const stageIndex = selectedStages.findIndex(
      (stage) => stage.number === stageNumber
    );

    selectedStages.splice(stageIndex, 1);
  }
}

function isStagePassed(stage) {
  return stage.services.length === 0;
}

function isHaveFreePlaces(stage) {
  let result = true;
  stage.services.forEach(service => {
    if (service.records_count === service.capacity) {
      result = false
    }
  });

  return result
}
onUnmounted(() => {
  store.state.steps.stepsFields.selectedStages = [];
});
</script>

<style lang="scss" scoped>
.select-command {
  display: flex;
  flex-direction: column;
  font-weight: 500;
  gap: 45px;
  button:first-child {
    margin-right: 147px;
  }

  @media (max-width: 500px) {
    gap: 35px;
  }
}
</style>
