<template>
  <div class="custom-radio">
    <input
      type="radio"
      :id="id"
      :name="name"
      :value="value"
      :checked="isChecked"
      @change="handleChange"
      :aria-checked="isChecked"
      :disabled="disabled"
      role="radio"
    />
    <label :for="id">
      <span class="radio-button" aria-hidden="true"></span>
      <span class="radio-label">
        <slot></slot>
      </span>
    </label>
  </div>
</template>

<script setup lang="ts">
import { computed, defineEmits, defineProps } from "vue";

const props = defineProps<{
  modelValue: string;
  value: string;
  name: string;
  id: string;
  disabled?: boolean
}>();

const emit = defineEmits(["update:modelValue"]);

const isChecked = computed(() => props.modelValue === props.value);

const handleChange = () => {
  emit("update:modelValue", props.value);
  emit("updateValue", isChecked);
};
</script>

<style lang="scss" scoped>
.custom-radio {
  display: flex;
  align-items: center;
  user-select: none;

  input[type="radio"] {
    display: none;
  }

  label {
    display: flex;
    align-items: flex-start;
    font-weight: 500;
  }

  .radio-label {
    font-weight: 500;
    line-height: 150%;
  }

  .radio-button {
    min-width: 16px;
    min-height: 16px;
    border: 1px solid var(--disabled-color);
    border-radius: 50%;
    position: relative;
    display: inline-block;
    transition: .1s;
    margin-right: 7px;
    margin-top: 3px;
    transform: translateY(-1px);
    cursor: pointer;
  }
  input[type="radio"]:checked + label {
    .radio-button {
      border: 4.5px solid var(--main-red);
    }
  }

  input[type="radio"]:disabled + label {
    .radio-button {
      cursor: default;
    }

    color: var(--disabled-color);
    .radio-label {
      cursor: default;
    }
  }

  .radio-label {
    cursor: pointer;
  }
}
</style>
