<template>
  <div class="custom-checkbox">
    <input
      type="checkbox"
      :id="id"
      :name="name"
      :value="value"
      :checked="isChecked"
      @change="handleChange"
      :disabled="disabled"
      role="checkbox"
    />
    <label :for="id">
      <span class="checkbox-box" aria-hidden="true"></span>
      <span class="checkbox-label">
        <slot></slot>
      </span>
    </label>
  </div>
</template>

<script setup lang="ts">
import { computed, defineEmits, defineProps } from "vue";

const props = defineProps({
  modelValue: Boolean,
  value: String,
  name: String,
  id: String,
  disabled: Boolean,
});

const emit = defineEmits(["update:modelValue"]);

const isChecked = computed(() => props.modelValue);

const handleChange = () => {
  emit("update:modelValue", !props.modelValue);
  emit("updateValue", !props.modelValue);
};
</script>

<style lang="scss" scoped>
.custom-checkbox {
  display: flex;
  align-items: center;
  user-select: none;

  input[type="checkbox"] {
    display: none;
  }

  label {
    display: flex;
    align-items: flex-start;
    font-weight: 500;
  }

  .checkbox-box {
    min-width: 16px;
    min-height: 16px;
    border-radius: 6px;
    border: 1px solid var(--disabled-color);
    position: relative;
    display: inline-block;
    transition: 0.1s;
    margin-right: 7px;
    margin-top: 3px;
    transform: translateY(-1px);
    cursor: pointer;
  }

  .checkbox-label {
    font-weight: 500;
    line-height: 150%;
    cursor: pointer;
  }

  input[type="checkbox"]:checked + label .checkbox-box {
    border-color: transparent;
    background: url(~@/assets/images/check.svg) no-repeat center / 105%;
  }

  input[type="checkbox"]:focus-visible + label .checkbox-box:not(:disabled) {
    outline: solid 1px #000;
  }

  input[type="checkbox"]:disabled + label {
    .checkbox-box {
      cursor: default;
    }
    color: var(--disabled-color);
  }
}
</style>
