<template>
  <div class="select-instructor">
    <div class="select-instructor__row">
      <radio-input
        name="selectInstructor"
        id="selectInstructor1"
        :value="'on'"
        v-model="instructorVisible"
        >С инструктором</radio-input
      >
      <radio-input
        name="selectInstructor"
        id="selectInstructor2"
        :value="'off'"
        v-model="instructorVisible"
        >Без инструктора</radio-input
      >
    </div>
    <div v-if="instructorVisible === 'on'">
      <label class="label-title" for="texarea47653">Комментарий:</label>
      <textarea
        v-model="store.state.steps.stepsFields.comment"
        placeholder="Есть любимый инструктор? Укажите имя — постараемся его назначить"
        class="default-textarea"
        id="texarea47653"
      ></textarea>
    </div>
  </div>
</template>

<script lang="ts" setup>
import { useStore } from "vuex";
import radioInput from "@/components/UI/radio-input.vue";
import { onUnmounted, ref, watch } from "vue";
const store = useStore();

const instructorVisible = ref(null);

watch(instructorVisible, () => {
  store.state.steps.stepsFields.instructor = instructorVisible.value === 'on'
})

onUnmounted(() => {
  store.state.steps.stepsFields.instructor = null;
  store.state.steps.stepsFields.comment = ''
})
</script>

<style lang="scss" scoped>
.select-instructor {
  font-size: 13px;
  &__row {
    display: flex;
    align-items: start;
    gap: 9%;
    margin-bottom: 26px;
  }
  .custom-radio {
    width: 40%;
  }

  @media (max-width: 500px) {
    &__row {
      gap: 0px;
    }
    .custom-radio {
      width: 50%;
    }
  }
}
</style>
